.Gallery {
    display: -ms-flexbox;
    display: flex;
    max-width: 100%;
    overflow-x: scroll;
    -webkit-overflow-scrolling: touch;
    overflow-y: hidden;
}

.Gallery::-webkit-scrollbar { 
    display: none; 
}

.Gallery .singleImage {
    margin-right: 10px;
    height: 100%;
    position: relative;
}

.SmallCard .Gallery .singleImage {
    margin-right: 5px;
}

.Gallery .singleImage:last-child {
    margin-right: 0px;
}

.react-swipeable-view-container .singleImage {
    
}


