/* TEPOGRAPHY */

body {
  font-family: avenir, sans-serif;
}

h1 {
    text-align: center;
    text-transform: uppercase;
    font-size: 20px;
    font-weight: 900;
  }
  
  .lightishText {
    color: rgba(0,0,0, .6)
  }
  
  .font-sm {
    font-size: 14px
  }
  
  .font-xs {
    font-size: 12px
  }
  
  .textLeft {
    text-align: left;
  }
  
  .textCenter {
    text-align: center;
  }

  .textRight {
    text-align: right;
  }


