/* 
======================================
slideOutUp
======================================
*/
@-webkit-keyframes slideOutUp {
    from {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }
  
    to {
      visibility: hidden;
      -webkit-transform: translate3d(0, -100%, 0);
      transform: translate3d(0, -100%, 0);
    }
  }
  
  @keyframes slideOutUp {
    from {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }
    
    to {
      visibility: hidden;
      -webkit-transform: translate3d(0, -100%, 0);
      transform: translate3d(0, -100%, 0);
    }
  }
  
  .slideOutUp {
    -webkit-animation-name: slideOutUp;
    -webkit-animation: slideOutUp 250ms ease-out forwards;
            animation: slideOutUp 250ms ease-out forwards;
  }




/* 
======================================
slideInDown
======================================
*/
@-webkit-keyframes slideInDown {
    from {
      -webkit-transform: translate3d(0, -100%, 0);
      transform: translate3d(0, -100%, 0);
      opacity: 1;
      visibility: visible;
    }
  
    to {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }
  }


  @keyframes slideInDown {
    from {
      -webkit-transform: translate3d(0, -100%, 0);
      transform: translate3d(0, -100%, 0);
      opacity: 1;
      visibility: visible;
    }
  
    to {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }
  }
  
  .slideInDown {
    -webkit-animation-name: slideInDown;
    animation-name: slideInDown;
    -webkit-animation: slideInDown 350ms ease-in-out forwards;
            animation: slideInDown 350ms ease-in-out forwards;
  }



/* 
======================================
pageTransition
======================================
*/
@-webkit-keyframes pageTransition {
  from {
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 5%, 0);
    visibility: visible;
    opacity: 0;
  }

  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

@keyframes pageTransition {
  0% {
    -webkit-transform: translateY(5%);
    transform: translateY(5%);
    visibility: visible;
    opacity: 0;
  }

  99% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
  }

  100% {
    -webkit-transform: none;
    transform: none;
  }
}

.pageTransition {
  -webkit-animation: pageTransition 350ms ease-in-out forwards;
          animation: pageTransition 350ms ease-in-out forwards;
}


