.MenuView{
    height: 0px;
    position: fixed;
    right: 0;
    left: 0;
    top: 0;
    z-index: 1; 
}

.MenuView.open {
    bottom: 0;
    z-index: 1;
    height: auto;
}

.MenuView .MenuHead {
    display: -ms-flexbox;
    display: flex;
    padding: 13px 25px 10px;
    left: 0;
    top: 0;
    z-index: 2;
    min-height: 26px;
}

.MenuView .MenuHead .icon,
.MenuView .MenuHead .logo {
    width: 28px;
    text-align: center;
}

.MenuView .MenuHead .icon {
    max-height: 22px;
}

.MenuView .MenuHead .title {
    -ms-flex-positive: 1;
        flex-grow: 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    position: relative;
    top: 1px;
}


.MenuView,
.MenuView .main {
    text-align: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.MenuView .menuBody {
    z-index: 0;
}

.MenuView.open .menuBody {
    padding-top: 3px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    z-index: 1;
    height: calc(100vh - 49px);
}

.MenuView .main  {
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.MenuView .main .item {
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    display: -ms-flexbox;
    display: flex;
    border-bottom: 1px solid rgba(0, 0, 0, .5)
}

.MenuView .main .item:last-child {
    border-bottom: none;
}

.MenuView .main .item {
    max-height: 70px
}  


