.Image {
    display: -ms-inline-flexbox;
    display: inline-flex;
    height: 100%;
    position: relative;
}

.Image img {
    height: 100%;
}

.Image .download {
    position: absolute;
    right: 0;
    bottom: 0;
    padding: 10px 0 0 10px;
    opacity: .5;
}

.react-swipeable-view-container .Image .download {
    -webkit-transform: scale(2);
        -ms-transform: scale(2);
            transform: scale(2);
    -webkit-transform-origin: right bottom;
        -ms-transform-origin: right bottom;
            transform-origin: right bottom;
}

.Image .download .downloadImage {
    height: 11px;
    position: relative;
    bottom: 4px;
}


