.extandedWrap {
    margin: 10px !important;
    max-height: calc(100% - 20px) !important;
}

.extandedWrap .galleryWrap {
    margin: 0 -10px
}

.BigCard {
    text-align: center
}

.SmallCard .cardBody,
.SmallCard .cardHeader,
.walker .cardHeader {
    display: -ms-flexbox;
    display: flex;
}

.SmallCard .cardHeader .item,
.walker .cardHeader .item {
    -ms-flex: 1 1;
        flex: 1 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.SmallCard .cardBody .walkNoteWrap,
.SmallCard .cardBody .galleryWrap {
    width: calc(50% - 5px);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.SmallCard .cardBody .walkNoteWrap {
    padding: 0 5px 0 0;
}

.SmallCard .cardBody .galleryWrap  {
    padding-left: 5px;
    overflow: scroll;
    -webkit-overflow-scrolling: touch;
}

.SmallCard .cardBody .galleryWrap::-webkit-scrollbar {
    display: none;
}

.SmallCard .cardBody .walkNoteWrap div{
    height: 17px;
    white-space: nowrap;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    padding-top: 13px;
    width: 100%;
}

.CardActions {
    display: -ms-flexbox;
    display: flex;
}

.CardActions img {
    height: 16px;
}



