a {
  color: inherit;
  text-decoration: none;
}

* {
  -webkit-transition: all 300ms ease-in-out;
  -o-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out;
}

body {
  margin: 0;
  padding: 0;
}

.page {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  -webkit-animation: slideInUp 350ms ease-in-out forwards;
          animation: slideInUp 350ms ease-in-out forwards;
}

.withMenu {
  padding-top: 49px;
}


